.server-list {

    width: 400px;

    .db-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        .button {
            button {
                padding: 4px;
            }
        }
    }

    .ant-drawer-body {
        padding: 12px;
    }

    .panel-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .server-list-header {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 48px;
        padding: 0 30px;
        margin: 10px 0;

        > .left {
            // width: 50%;
        }

        > .right {
            button {
                margin: 0 10px;
            }
        }
    }
}
